import App from "./core"
import Element from "./Element"
import EventListener from "./EventListener"

interface mobileMenuSettings {
	burger: string,
	menu: string,
	menuActiveClass: string,
	bodyActiveClass: string,
	ignoreWarnings: boolean,
	fixBody: boolean,
	media?: string,
}

export default class MobileMenu{
	private _settings: mobileMenuSettings
	private _burger: HTMLElement
	private _menu: HTMLElement
	private _state: boolean = false
	private _error: boolean = false
	private menuActiveClass: string = "js__opened"
	private bodyActiveClass: string = "js__menu-opened"
	private body: HTMLElement = App.getElement("body")
	private ignoreWarnings: boolean = false

	set error(text: string){
		this._error = true

		console.error(`${text}. Меню не работает`)
	}

	set state(newState: boolean){
		this._state = newState
	}


	set menu (el: HTMLElement){
		if (!(el instanceof HTMLElement) && !this.ignoreWarnings)
			this.error = "Меню не найдено"
		else
			this._menu = el
	}

	set burger(el: HTMLElement){
		if (!(el instanceof HTMLElement) && !this.ignoreWarnings)
			this.error = "Бургер не найден"
		else
			this._burger = el
	}

	get burger(){
		return this._burger
	}

	get menu(){
		return this._menu
	}

	get settings(){
		return this._settings
	}

	get state(){
		return this._state
	}

	constructor(settings: mobileMenuSettings){
		this._settings = settings
		this.ignoreWarnings = settings.ignoreWarnings


		this.burger = App.getElement(settings.burger)

		this.menuActiveClass = settings.menuActiveClass
		this.bodyActiveClass = settings.bodyActiveClass

		this.menu = App.getElement(settings.menu)

		this.bindEvents()
	}

	public openMenu(): MobileMenu{
		if (!window.matchMedia(this.settings.media).matches)
			return

		if (this.settings.fixBody){
			this.body.style.top = -window.pageYOffset + "px";
			this.body.style.position = "fixed";
		}

		document.querySelector(this.settings.burger).classList.add("js__active")
		document.querySelector(this.settings.menu).classList.add(this.menuActiveClass)
		this.body.classList.add(this.bodyActiveClass)

		this.state = true;

		if (window.matchMedia("(max-width: 1000px)").matches) {
			const header = document.querySelector(".main-aside") as HTMLElement;

			const headerRect = header.getBoundingClientRect();


			this.menu.style.top = `${headerRect.height}px`;

		} else {
			this.menu.style.top = `0px`;

			
			
		}

		return this
	}

	public closeMenu(): MobileMenu{
		if (!window.matchMedia(this.settings.media).matches || !this.state)
			return

		let top: number = 0;

		if (this.settings.fixBody){
			top = Math.abs(parseInt(this.body.style.top))

			this.body.style.top = ""
			this.body.style.position = ""
		}

		document.querySelector(this.settings.burger).classList.remove("js__active")
		document.querySelector(this.settings.menu).classList.remove(this.menuActiveClass)
		this.body.classList.remove(this.bodyActiveClass)

		if (this.settings.fixBody)
			window.scrollTo(0, top)

		this.state = false

		return this
	}

	public toggleMenu(): MobileMenu{
		if (!window.matchMedia(this.settings.media).matches)
			return
		
		if (this.state)
			this.closeMenu()
		else
			this.openMenu()

		return this
	}

	private bindEvents(){		
		document.addEventListener("click", (event: Event) => {
			if (
				event.target instanceof SVGAElement
				|| event.target instanceof SVGElement
				|| event.target instanceof SVGPathElement
				|| event.target instanceof SVGGElement
			)
				return;


			const target: Element = new Element(event.target);
			
			
			if(!target.is(this.settings.burger)
				&& !new Element(this.settings.burger).has(target)
				&& !target.is(this.settings.menu)
				&& !new Element(this.settings.menu).has(target))
				this.closeMenu();
			
			if (
				target.is(this.settings.burger)
				|| new Element(this.settings.burger).has(target)
			){
				this.toggleMenu();
			}
		})

		document.addEventListener("keyup", (e: KeyboardEvent) => {
			if (e.key == "Escape")
				this.closeMenu();
		});

		
		// this.burger.addEventListener("click", this.toggleMenu.bind(this));

		// new EventListener(this.settings.burger).add("click", (el: HTMLElement) => {
		// 	this.toggleMenu()
		// });
	}
}